            {text: "饭前", value: "2"},
            {text: "饭中", value: "3"},
            {text: "睡前", value: "4"},
            {text: "每4小时", value: "5"},
            {text: "每8小时", value: "6"},
            {text: "每12小时", value: "7"},
            {text: "每天", value: "8"},
            {text: "隔天", value: "9"}
